@font-face {
  font-family: 'Kasaet';
  src: url('/fonts/Kasaet-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Kasaet';
  src: url('/fonts/Kasaet-ExtraBold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
.dreamy-filter {
  filter: saturate(0.9) brightness(1.05) contrast(0.95);
}
@keyframes bounce-soft {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}
.animate-bounce-soft {
  animation: bounce-soft 2s ease-in-out infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0) rotate(0deg);
  }
  33% {
    transform: translateY(-15px) rotate(2deg);
  }
  66% {
    transform: translateY(-8px) rotate(-1deg);
  }
}
.animate-float {
  animation: float 6s ease-in-out infinite;
}
.animate-float-delay {
  animation: float 8s ease-in-out 1s infinite;
}
.animate-float-slow {
  animation: float 10s ease-in-out 2s infinite;
}
@keyframes petal-fall {
  0% {
    transform: translateY(-10vh) translateX(0) rotate(0deg);
    opacity: 1;
  }
  25% {
    transform: translateY(22vh) translateX(15px) rotate(90deg);
    opacity: 0.9;
  }
  50% {
    transform: translateY(50vh) translateX(-10px) rotate(180deg);
    opacity: 0.7;
  }
  75% {
    transform: translateY(75vh) translateX(20px) rotate(270deg);
    opacity: 0.4;
  }
  100% {
    transform: translateY(105vh) translateX(5px) rotate(360deg);
    opacity: 0;
  }
}
.animate-petal-fall {
  animation: petal-fall linear infinite;
  will-change: transform, opacity;
}
@keyframes spin-slow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.animate-spin-slow {
  animation: spin-slow 3s linear infinite;
}
@keyframes shimmer {
  0% {
    background-position: -200% center;
  }
  100% {
    background-position: 200% center;
  }
}
.text-gold-shimmer {
  background: linear-gradient(90deg,
      #D4AF37 0%,
      #E8D48B 25%,
      #D4AF37 50%,
      #E8D48B 75%,
      #D4AF37 100%);
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: shimmer 4s linear infinite;
}
.polaroid-card {
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.polaroid-card:hover {
  transform: rotate(0deg) scale(1.08) !important;
  z-index: 10;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
}
::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #FCF8F4;
}
::-webkit-scrollbar-thumb {
  background: #D4A3A3;
  border-radius: 3px;
}
::-webkit-scrollbar-thumb:hover {
  background: #B87A7A;
}
.flip-card {
  perspective: 400px;
}
.flip-card-inner {
  position: relative;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}
.lightbox-overlay {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}
.ornament-divider {
  display: flex;
  align-items: center;
  gap: 16px;
}
.ornament-divider::before,
